import * as particles from "./particle";
import * as updatable from "./lib/updatable";
import _instance from 'three-instanced-mesh';
import {
    InitParticleScenes
} from "./behaviourScene";
var InstancedMesh = _instance(window.THREE);

export function build() {
    var mainGrp = new THREE.Group();
    mainGrp.opaViz = 0;
    mainGrp.opaViz_t = 1;
    var ball = new THREE.SphereGeometry(201, 55, 55);
    var ballmat = new THREE.MeshPhysicalMaterial({
        color: 0x223344,
        shading: THREE.FlatShading,
        transparent: true,
        opacity: 1,
        metalness: 0.99,
        // wireframe: true,
        side: THREE.BackSide,
        envMap: resources.env,
        envMapIntensity: 0.1
    });
    var ballMesh = new THREE.Mesh(ball, ballmat);
    var ball2 = new THREE.SphereGeometry(200, 55, 55);
    var ballmat2 = new THREE.MeshBasicMaterial({
        color: 0xffffff,
        transparent: true,
        opacity: 0.1,
        wireframe: true,
        side: THREE.BackSide
    });
    var ballMesh2 = new THREE.Mesh(ball2, ballmat2);
    var tilt_feel = new THREE.Group();
    tilt_feel.add(ballMesh);
    tilt_feel.add(ballMesh2);
    mainGrp.add(tilt_feel);
    updatable.enable(() => {
        mainGrp.opaViz = updatable.ease(mainGrp.opaViz, mainGrp.opaViz_t, 0.1, 0.0001);
        tilt_feel.rotation.x = -control.y / 10;
        tilt_feel.rotation.y = -control.x / 10;
        // ballMesh.rotation.y += 0.001;
        // ballMesh2.rotation.y += 0.001;
        ballmat.opacity = mainGrp.opaViz;
        ballmat2.opacity = mainGrp.opaViz * 0.1;
        ballmat.needsUpdate = ballmat2.needsUpdate = true;
    });

    //core shape
    var mat = new THREE.MeshPhysicalMaterial({
        color: 0xffffff,
        clearCoat: 1,
        clearCoatRoughness: 0,
        roughness: 0,
        metalness: 0.1,
        envMap: resources.env,
        envMapIntensity: 0.7
    });

    var instancedMesh = new THREE.InstancedMesh(
        resources.plate.children[0].geometry,
        mat,
        points.length,
        true,
        true,
        //is the scale known to be uniform, will do less shader work, improperly applying this will result in wrong shading 
        false
    );

    instancedMesh.visible = true;
    instancedMesh.castShadow = false;
    instancedMesh.receiveShadow = false;
    var init_scale = new THREE.Vector3(0.2, 0.2, 0.2);

    var particleSystem = [];
    for (var i = 0; i < points.length; i++) {
        var p = new particles.p_fly(points[i], instancedMesh);
        particleSystem.push(p);
    }

    InitParticleScenes(particleSystem);

    var ex = 0;
    var ey = 0;
    var rotE = 0.05;


    var _t = 0;

    function spin(a, b) {
        if (a - b > 2) {
            a -= 2;
        } else if (b - a > 2) {
            a += 2;
        }
        return a;
    }
    updatable.enable(() => {
        _t += 0.01;

        if (control.useLocalXY > 0) {
            ex = spin(ex, control.localX);
            ey = spin(ey, control.localY);
            ex = updatable.ease(ex, control.localX, rotE, 0.00001);
            ey = updatable.ease(ey, control.localY, rotE, 0.00001);
        } else {
            ex = spin(ex, control.x);
            ey = spin(ey, control.y);
            ex = updatable.ease(ex, control.x, rotE, 0.00001);
            ey = updatable.ease(ey, control.y, rotE, 0.00001);
        }

        for (var i = 0; i < particleSystem.length; i++) {
            particleSystem[i].update(1 / 30, _t);
        }
        instancedMesh.needsUpdate('position');
        instancedMesh.needsUpdate('scale');
        instancedMesh.needsUpdate('quaternion');
        instancedMesh.needsUpdate('color');
        instancedMesh.rotation.x = ey * Math.PI * 2;
        instancedMesh.rotation.y = ex * Math.PI * 2;

    })

    mainGrp.add(instancedMesh);
    window.mainGrp = mainGrp;
    return mainGrp;
}