<template>
  <div class="pin" v-bind:style="style">
    <div class="dot"></div>
    <div class="txt" v-bind:style="textStyle">
      <div class="title">{{patent.name}}</div>
      <div class="desc"> / {{patent.manage_id}}</div>
      <div class="desc"> / {{patent.id}}</div>
      <div class="detail">{{patent.desc}}</div>
    </div>
  </div>
</template>

<script>
import * as updatable from "../lib/updatable";
export default {
  props: ["patent"],
  data: function() {
    return {
      x: 0,
      y: 0,
      z: 0,
      topMost: 0
    };
  },
  computed: {
    style: function() {
      var o = 0;
      if (window.control.mode == 2) {
        if (this.z > 0) {
          o = (this.z / window.topMostPatent.world_position[2]) * 0.2;
          if (this.topMost) {
            o = 1;
          }
        }
      }
      return {
        opacity: o,
        transform: `translate(${this.x}px, ${this.y}px)`
      };
    },
    textStyle: function() {
      return {
        opacity: this.topMost && this.z > 0 ? 1 : 0
      };
    }
  },
  mounted: function() {
    updatable.enable(() => {
      if (window.control.mode == 2) {
        var pt = project_arr(this.patent.world_position);
        this.x = (pt.x * window.data.width) / 2 + window.data.width / 2;
        this.y = -(pt.y * window.data.height) / 2 + window.data.height / 2;
        this.patent.dist = Math.abs(pt.x) + Math.abs(pt.y);
        this.z = this.patent.world_position[2];
        this.topMost = window.closestPatent == this.patent ? 1 : 0;

        if (!window.closestPatent) {
          window.closestPatent = this.patent;
        } else if (this.patent.dist < window.closestPatent.dist && this.z > 0) {
          window.closestPatent = this.patent;
        }
      }
    });
  }
};
</script>

<style lang="less" scoped>
@sz: 30px;
.detail {
  margin-top: 18px;
  width: 560px;
  font-size: 0.9rem;
  line-height: 1.5rem;
  font-weight: 200;
}
