  transition: 0.5s opacity ease;
  position: fixed;
  color: white;
  font-size: 20px;
  font-weight: 600;
  transform: translate(300px, 300px);
}

.txt {
  position: absolute;
  transition: 0.5s opacity ease;
  transform: translate(40px, -20px);
}

.dot {
  width: @sz;
  height: @sz;
  transform: translate(-50%, -50%);
  border: 5px solid white;
  border-radius: 10000px;
}

.title {
  font-size: 1.6rem;
  max-width: 200px;
  min-width: 300px;
  margin-bottom: 10px;
}

.desc {
  font-size: 0.9rem;
  opacity: 0.7;
}
</style>